import React from 'react'
import { useFormik } from 'formik';
import * as Yup from 'yup';





const Signup = () => {

    const formik = useFormik({
        initialValues: {
            numeroUnidade: '1',
            nomeUnidade: '',
            telefoneUnidade: '',
            regiaoUnidade: '',
            emailGerente: '',
            nomeGerente: '',
            telefoneGerente: '',
            cpfGerente: '',

        },
        validationSchema: Yup.object({
            numeroUnidade: Yup.number().min(1, 'Não existe zero ou menos de unidades').required('obrigatorio'),
            nomeUnidade: Yup.string().max(15, 'Deve ter 15 caracteres ou menos').required('obrigatorio'),
            telefoneUnidade: Yup.string().max(12, 'max 12 characteres').required('obrigatorio'),
        }),
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2));
        },
    });

    return (
        <div class="flex flex-col justify-center justify-items-center bg-gray-500 h-screen px-8" >
            <form onSubmit={formik.handleSubmit}>
                <div class=" h-48 grid grid-cols-4 grid-rows-3 items-center justify-items-center border-r-2 border-l-2 border-t-2 h-full">

                    <div class=" flex flex-col items-center w-30 ">
                        <label class="flex" htmlFor="numeroUnidade">Numero da Unidade</label>
                        <input class="flex w-7 mt-2 text-center rounded-md"
                            id="numeroUnidade"
                            name="numeroUnidade"
                            type="number"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.numeroUnidade}
                        />
                        {formik.touched.numeroUnidade && formik.errors.numeroUnidade ? (<div> {formik.errors.numeroUnidade} </div>) : null}
                    </div>

                    <div class="flex flex-col items-center  w-45 ">
                        <label class="flex" htmlFor="email">Nome da Unidade</label>
                        <input class="flex text-center mt-2 border-none w-45 rounded-md"
                            id="nomeUnidade"
                            name="nomeUnidade"
                            type="texto"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.nomeUnidade}
                            maxLength="15"
                        />
                        {formik.touched.nomeUnidade && formik.errors.nomeUnidade ? (<div> {formik.errors.nomeUnidade} </div>) : null}
                    </div>

                    <div class="flex flex-col items-center ">
                        <label class="flex" htmlFor="email">Região da Unidade</label>
                        <input class="flex text-center mt-2 border-none w-45 rounded-md"
                            id="regiaoUnidade"
                            name="regiaoUnidade"
                            type="texto"
                            onChange={formik.handleChange}
                            value={formik.values.regiaoUnidade}
                        />
                    </div>

                    <div class="flex flex-col items-center ml-8 mr-8">
                        <label class="flex" htmlFor="email">Telefone da Unidade</label>
                        <input class="flex text-center border-none mt-2 w-22 rounded-md"
                            id="telefoneUnidade"
                            name="telefoneUnidade"
                            type="texto"
                            onChange={formik.handleChange}
                            value={formik.values.telefoneUnidade}
                        />
                        {formik.errors.telefoneUnidade ? (<div>{formik.errors.telefoneUnidade}</div>) : null}
                    </div>

                    <div class="flex flex-col items-center ml-8 mr-8 ">
                        <label class="flex" htmlFor="email">Email do Gerente</label>
                        <input class="flex text-center border-none mt-2 w-80 rounded-md"
                            id="emailGerente"
                            name="emailGerente"
                            type="email"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.emailGerente}
                        />
                        {formik.touched.emailGerente && formik.errors.emailGerente ? (<div>{formik.errors.emailGerente}</div>) : null}
                    </div>

                    <div class="flex flex-col items-center ml-8 mr-8 ">
                        <label class="flex" htmlFor="email">Nome do Gerente</label>
                        <input class="flex text-center border-none mt-2 w-80 rounded-md"
                            id="nomeGerente"
                            name="nomeGerente"
                            type="texto"
                            onChange={formik.handleChange}
                            value={formik.values.nomeGerente}
                        />
                    </div>

                    <div class="flex flex-col items-center ml-8 mr-8 ">
                        <label class="flex" htmlFor="email">Numero de telefone do Gerente</label>
                        <input class="flex text-center border-none mt-2 w-80 rounded-md"
                            id="telefoneGerente"
                            name="telefoneGerente"
                            type="texto"
                            onChange={formik.handleChange}
                            value={formik.values.telefoneGerente}
                        />
                        {formik.errors.telefoneGerente ? (<div>{formik.errors.telefoneGerente}</div>) : null}
                    </div>

                    <div class="flex flex-col items-center ml-8 mr-8 ">
                        <label class="flex" htmlFor="email">Numero de CPF do Gerente</label>
                        <input class="flex text-center border-none mt-2 w-80 rounded-md"
                            id="cpfGerente"
                            name="cpfGerente"
                            type="texto"
                            onChange={formik.handleChange}
                            value={formik.values.cpfGerente}
                        />
                    </div>




                    <div class="flex flex-col items-center">
                        <button type="submit">Submit</button>
                    </div>
                </div>

            </form>
            <div class="flex justify-center mt-32">
                Increver
            </div>
        </div>
    );
};

export default Signup
