import React from 'react'
import { useFormik } from 'formik';

//Formularios Teste

const Signip = () => {

    const formik = useFormik({
        initialValues: {
            email: '',
        },
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2));
        },
    });

    return (
        <div class="flex flex-col justify-center justify-items-center bg-gray-500 h-screen" >
            <form class="flex justify-center mt-32" onSubmit={formik.handleSubmit}>
                <label htmlFor="email">Email Address</label>
                <input class="flex w-96 ml-8 mr-8 text-center border-none"
                    id="email"
                    name="email"
                    type="email"
                    onChange={formik.handleChange}
                    value={formik.values.email}
                />
                <button type="submit">Submit</button>

            </form>
            <div class="flex justify-center mt-32">
                Increver
            </div>
        </div>
    );
};

export default Signip
